var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/1.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"bg-color-1 p-5 mb-3",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("La toxicología industrial es una rama especializada de la toxicología aplicada, que se dedica al estudio de los efectos nocivos que pueden ocasionar diversas sustancias químicas presentes en los ambientes laborales, sobre la salud de los trabajadores. Esta disciplina tiene como finalidad fundamental comprender, evaluar y mitigar los riesgos asociados a la exposición a agentes tóxicos en el entorno laboral, con el objetivo de promover condiciones de trabajo seguras y proteger la salud ocupacional de los empleados. La toxicología industrial combina conocimientos de química, medicina, biología y ergonomía para desarrollar estrategias eficaces de prevención, control y tratamiento de las posibles afecciones relacionadas con la exposición a sustancias peligrosas en diferentes industrias y sectores económicos.")])]),_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Los trabajadores industriales, en su día a día, están expuestos a una amplia variedad de sustancias químicas peligrosas, incluyendo disolventes orgánicos, metales pesados como plomo, mercurio o arsénico, pesticidas utilizados en la agricultura, gases tóxicos, vapores inflamables y partículas en suspensión. La exposición prolongada o aguda a estos agentes puede tener efectos graves sobre la salud, desde irritaciones leves en las vías respiratorias hasta enfermedades crónicas, daños en órganos internos, alteraciones neurológicas o incluso cáncer. La toxicología industrial investiga en detalle cómo estas sustancias interactúan con el organismo humano a nivel molecular, celular y sistémico, identificando los mecanismos fisiopatológicos que desencadenan las diferentes lesiones o enfermedades. Además, evalúa los niveles de exposición seguros, las vías de entrada al cuerpo (como inhalación, ingestión o absorción cutánea) y las condiciones que aumentan el riesgo de efectos adversos.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 order-lg-2 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/2.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8 order-lg-1"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Asimismo, esta disciplina no solo se enfoca en la identificación y evaluación de los riesgos, sino que también desarrolla y recomienda medidas preventivas, tales como el uso de equipos de protección personal adecuados, la implementación de controles de ingeniería en los procesos productivos, la vigilancia epidemiológica de los trabajadores y programas de capacitación en seguridad laboral. ")]),_c('div',{staticClass:"bg-color-8 p-5 mb-3",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"row align-items-start"},[_c('div',{staticClass:"col-lg-auto"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema1/3.svg")}})]),_c('div',{staticClass:"col-lg"},[_c('p',{staticClass:"mb-0"},[_vm._v("La toxicología industrial, por tanto, juega un papel crucial en la protección de la salud de los empleados, ayudando a reducir la incidencia de enfermedades relacionadas con la exposición ocupacional y promoviendo entornos laborales más seguros y saludables. En un contexto donde la industrialización continúa creciendo, la importancia de esta rama de la toxicología se vuelve aún más relevante para garantizar condiciones laborales que minimicen los riesgos y fomenten el bienestar integral de la fuerza laboral.")])])])]),_c('p',[_vm._v("Los principios fundamental